<template>
  <div class="transfer-card">
    <div class="container">
      <div class="front">
        <slot name="front"></slot>
      </div>

      <div class="backend">
        <slot name="backend"></slot>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "TransferCard"
  }
</script>

<style scoped lang="less">
  .transfer-card{
    width: 200px;
    height: 200px;
    margin: 10px;

    .container{
      width: 100%;height: 100%;
      position: relative;
      perspective: 500px;
      -webkit-perspective: 500px;
      cursor: pointer;

      .front,.backend{
        backface-visibility: hidden; //该属性设置当元素翻转时其背面是否可见 hidden（不可见） visible（可见）
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;height: 100%;
        -webkit-transition: all 1.5s;
        -moz-transition: all 1.5s;
        transition: all 1.5s;
        -o-transition: all 1.5s;
        box-shadow: rgba(50, 50, 50, 0.2) 0 0 15px;
        overflow: hidden;
        -webkit-perspective: 1000px;
      }

      .front{
        background-color: #F9BA8A;
        display: flex;
        align-items: center;
        justify-content: center;
        i{
          font-size: 40px;
          color: #ffffff;
        }
      }

      .backend{
        background-color: #FF8080;
        color: @color-white;
        border: @color-border solid 1px;
        transform: rotateY(-180deg);
        -webkit-transform: rotateY(-180deg);
      }

    }

    .container:hover .front{
      transform: rotateY(-180deg);
      -webkit-transform: rotateY(-180deg);
    }

    .container:hover .backend {
      transform: rotateY(-360deg);
      -webkit-transform: rotateY(-360deg);
    }
  }
</style>