<template>
  <div class="skill-table">
    <div class="item" v-for="(item,index) in data" :key="item.key">
      <i-circle :percent="item.value" stroke-color="#0ACDC0"
                @mouseenter.native="enter(index)" @mouseleave.native="leave"
                @click.native="toBlog(item.key)">
        <div>
          <span v-if="index !== currentIndex" class="value">{{item.value}}%</span>
          <span v-else class="more">More</span>
        </div>
      </i-circle>

      <span class="key">{{item.key}}</span>
    </div>
  </div>
</template>

<script>
  export default {
    name: "SkillTable",
    props:{
      data:Array
    },
    data(){
      return{
        currentIndex:-1
      }
    },
    methods:{
      enter(index){
        this.currentIndex = index;
      },
      leave(){
        this.currentIndex = -1;
      },
      toBlog(key){
        console.log(key);
      }
    }
  }
</script>

<style scoped lang="less">
  .skill-table{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-around;
    .item{
      margin: 10px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      cursor: pointer;

      .value{
        font-size: 20px;
      }
      .more{
        font-size: 20px;
        font-weight: bold;
        color: @color-skyblue;
      }

      .key{
        padding-top: 8px;
        font-size: 16px;
        font-weight: bold;
        letter-spacing: 1px;
      }
    }
  }
</style>