<template>
  <div class="profile-data">
    <button class="md-open" @click="$emit('show')">
      <i class="fas fa-bars"></i>
    </button>
    <div class="about-me">
      <Describer title="关于我">
        <template #body>
          <b>你好，我叫杨豪。</b><br/>
          <p>首先呢，非常感谢有缘来到这里的朋友，给大家简单介绍一下我吧。</p>

          <Quoter>
            <p slot="body">年龄22岁，是个万年单身狗。目前大三，在河南大学软件学院就读，专业软件工程，学的嘛，嗯，还算可以吧。
              喜欢看动漫，偶尔会中二的说几句羞耻的话（好吧，不是偶尔是经常），当然如果旁边有不熟的人我还是挺正经的。
              当然也会打游戏，又菜又爱玩，王者荣耀在线掉分选手，原神锄大地第一名玩家。
            </p>
          </Quoter>

          <param-table :left="paramLeft" :right="paramRight"></param-table>
        </template>
      </Describer>
    </div>

    <div class="skill">
      <Describer title="技术栈">
        <template #body>
          <skill-table :data="skill"></skill-table>
        </template>
      </Describer>
    </div>

    <div class="likes">
      <Describer title="兴趣爱好">
        <template #body>
          <transfer-card-table></transfer-card-table>

          <Quoter>
            <p slot="body">
              未来还想抽空学学日语，作为兴趣培养起来，但如今程度马马虎虎，实在不值得讲出来，期待能有一个日语大佬与我在线陪聊，长长见识(●'◡'●)。
              <br/>
              ps：学日语只是兴趣使然，站主二次元浓度值偏高，见谅。
            </p>
          </Quoter>
        </template>
      </Describer>
    </div>

    <div class="experience">
      <Describer title="工作经历">
        <template #body>
          <Quoter>
            <p slot="body">
              emmm，暂无工作经历！毕竟我还没有毕业啊，可恶。<br/>
              不过先占个位置，嘿嘿。
            </p>
          </Quoter>
          <my-time-line></my-time-line>
        </template>
      </Describer>
    </div>

    <div class="contact-me">
      <Describer title="联系我">
        <template #body>
          <contact-table></contact-table>
        </template>
      </Describer>
    </div>
  </div>
</template>

<script>
  import Describer from "components/common/widgets/Describer";

  import Quoter from "./Quoter";
  import ParamTable from "./ParamTable";
  import SkillTable from "./SkillTable";
  import TransferCardTable from "./TransferCardTable";
  import MyTimeLine from "./MyTimeLine";
  import ContactTable from "./ContactTable";

  export default {
    name: "ProfileData",
    components:{
      Describer,
      Quoter,
      ParamTable,
      SkillTable,
      TransferCardTable,
      MyTimeLine,
      ContactTable
    },
    data(){
      return{
        paramLeft:[
          {key:'性别',value:'男'},
          {key:'年龄',value:'22'},
          {key:'专业',value:'软件工程'},
          {key:'就读院校',value:'河南大学'},
        ],
        paramRight:[
          {key:'生日',value:'7月27日'},
          {key:'身高',value:'174cm'},
          {key:'住址',value:'河南南阳'},
          {key:'现居地',value:'河南开封'},
        ],
        skill:[
          {key:'HTML/CSS',value:95},
          {key:'JavaScript',value:75},
          {key:'Python',value:90},
          {key:'Django',value:85},
          {key:'MySQL',value:78},
          {key:'Redis',value:60},
          {key:'Django restful',value:80},
          {key:'Spark',value:60},
          {key:'React',value:85},
          {key:'Vue.js',value:85},
        ]
      }
    }
  }
</script>

<style scoped lang="less">
  .profile-data{
    width: 100%;
    height: auto;
    padding: 25px 35px;
    margin-left: 20px;
    border-radius: 10px;
    border: @color-lightwhite solid 1px;
    background: @color-white;

    @media screen{
      @media (max-width: 568px){
        padding: 10px 5px;
        margin: 10px 0;
      }
      @media (min-width: 569px) and (max-width: 992px){
        padding: 10px 5px;
        margin-left: 0px;
      }
      @media (min-width: 993px) and (max-width: 1200px){
        padding: 15px 15px;
      }
    }

    .md-open{
      display: none;
      @media screen{
        @media (min-width: 569px) and (max-width: 992px){
          display: block;
          position: absolute;
          top: 10px;right: 10px;
          width: 40px;
          height: 32px;
          background: #0ACDC0;
          border-radius: 10px;
          cursor: pointer;
          i{
            color: @color-white;
          }
        }
      }
    }
  }
</style>