<template>
  <div class="profile-widget">
    <div class="avatar">
      <div class="me">
        <img src="~assets/img/avatar-me.jpg" alt="头像" style="width: 100%;" />
      </div>
    </div>

    <div class="name">
      <span class="desc">Hello,I'am YangHao!</span>
    </div>

    <div class="link">
      <i class="fab fa-github"></i>
      <i class="fab fa-git"></i>
      <i class="fab fa-qq"></i>
      <i class="fab fa-weixin"></i>
    </div>

    <div class="desc-nav">
      <item-cell v-for="item in itemData" :value="item.value" :key="item.value"
                :class="{'active':item.isActive}" @click.native="itemActive(item.value)">
        <i :class="item.icon" slot="icon"></i>
      </item-cell>
    </div>

    <div class="cv">
      <button class="hover-btn" @click="cvbtnClick">
        <i class="fas fa-download"></i>
        <span>Download CV</span>
      </button>
    </div>

  </div>
</template>

<script>
  /* 在profile-widget中糅合了其它组件，使之耦合度偏高，
   * 更好的做法应该是再封装一个只供本项目用的组件
   * 但为了节省开发效率，直接在该组件下调用其它组件 */

  import ItemCell from "./ItemCell";

  export default {
    name: "ProfileWidget",
    components:{
      ItemCell
    },
    data(){
      return{
        itemData:[
          {icon:'fas fa-address-card',value:'关于我',isActive:true},
          {icon:'fas fa-code',value:'技术栈',isActive:false},
          {icon:'fas fa-icons',value:'兴趣爱好',isActive:false},
          {icon:'fas fa-paper-plane',value:'工作经历',isActive:false},
          {icon:'fas fa-comment-dots',value:'联系我',isActive:false},
        ]
      }
    },
    methods:{
      itemActive(val){
        this.itemData.forEach(item => {
          item.isActive = item.value === val;
        })
      },
      cvbtnClick(){
        console.log('cvClick');
      }
    }
  }
</script>

<style scoped lang="less">
  @keyframes typing {
    from { width: 0; }
  }
  @keyframes caret {
    /* 控制文字指针光标闪烁的帧 */
    50% { border-color: transparent; }
  }
  @keyframes colorSkip {
    0%{

    }
    50%{

    }
    100%{
      background: @color-white;
      color: @color-skyblue;
      border: @color-skyblue solid 1px;
    }
  }
  @keyframes iconSkip {
    0% {
      transform: translate(0px, 0px);
    }
    50% {
      transform: translate(0px, -4px);
    }
    100% {
      transform: translate(0px, 0px);
    }
  }
  .profile-widget{
    width: 100%;
    height: 580px;
    margin-right: 10px;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    position: relative;
    background-color: #fff;
    overflow: hidden;
    -webkit-box-shadow: 0 2px 26px 0 rgba(133,153,171,0.1);
    box-shadow: 0 2px 26px 0 rgba(133,153,171,0.1);

    @media screen{
      @media (max-width: 568px){

      }
      @media (min-width: 569px) and (max-width: 992px){
        margin-right: 0;
      }
    }

    .avatar{
      background-color: @color-skyblue;
      position: relative;
      height: 120px;
      width: 100%;
      .me{
        position: absolute;
        display: block;
        width: 130px;
        height: 130px;
        background-color: #fff;
        -webkit-border-radius: 50%;
        border-radius: 50%;
        border: 5px solid #fff;
        bottom: -65px;
        left: 50%;
        margin-left: -65px;
        overflow: hidden;
        img {
          width: 100%;
          height: auto;
        }
      }
    }

    .name{
      margin: 75px 30px 5px 30px;
      padding-bottom: 5px;
      border-bottom: @color-skyblue solid 2px;
      text-align: center;

      .desc{
        display: block;
        position: relative;
        font-family: Grenze;
        font-size: 18px;
        letter-spacing: 1px;
        width: 167px;
        overflow: hidden;  //超出部分隐藏
        white-space: nowrap; //禁止换行
        border-right: 2px solid;
        animation: typing 6s steps(32), caret 0.8s steps(1) infinite;
      }
    }

    .desc-nav{
      padding: 10px 27px;
      .active{
        padding-left: 20px;
        color: @color-skyblue;
      }
    }

    .cv{
      padding-top: 10px;
      width: 100%;
      display: flex;
      justify-content: center;
      .hover-btn{
        width: 160px;
        height: 48px;
        border-radius: 12px;
        padding: 5px 10px;
        cursor: pointer;
        background: @color-skyblue;
        color: @color-white;
        &:hover,&:focus,&:active{
          animation: colorSkip 0.7s;
          animation-fill-mode: forwards;
          i{
            animation: iconSkip 0.3s infinite;
          }
        }
        i{
          font-size: 14px;
        }
        span{
          padding-left: 5px;
          font-size: 16px;
        }
      }
    }

    .link{
      padding: 20px 30px 10px 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      i{
        font-size: 22px;
        margin: 0 10px;
        cursor: pointer;
        &:hover{
          color: @color-skyblue;
        }
      }
    }

  }
</style>