<template>
  <div id="about">
    <scroll class="wrapper" ref="scroll" :probe-type="3" :listen-scroll="listenScroll"
            :pullup="true" :pulldown="true"
            @scroll="ContentScroll" @pullingUp="pullUp" @pullingDown="pullDown">
      <div class="content" style="padding-top: 20px;padding-bottom: 20px;">
        <Row type="flex" justify="center" align="top">
          <Col :xs="{span:23}" :sm="{span:0}" :lg="{span:7}" :xl="{span:4}" style="position: relative">
            <profile-widget :style="fixedStyle" id="www"></profile-widget>
          </Col>

          <Col :xs="{span:23}" :sm="{span:22}" :lg="{span:15}" :xl="{span:11}">
            <profile-data @show="showWidget"></profile-data>
          </Col>
        </Row>
      </div>
    </scroll>

    <Modal v-model="showModal" :footer-hide="true" :styles="{padding: 0,border:0}" :closable="false">
      <profile-widget></profile-widget>
    </Modal>

  </div>
</template>

<script>
  import ProfileWidget from "components/common/widgets/ProfileWidget";
  import ProfileData from "./childcpns/ProfileData";

  import Scroll from "components/common/Scroll/Scroll";

  export default {
    name: "About",
    components:{
      ProfileWidget,
      ProfileData,
      Scroll,
    },
    data(){
      return{
        device_width:document.body.clientWidth || document.documentElement.clientWidth,
        showModal:false,
        listenScroll:true, //是否监听scroll
        fixedStyle:{
          position:'',
          width:''
        }
      }
    },
    mounted() {
      /* 首先判断当前浏览器环境：1）pc环境：监听鼠标滚动事件 2）移动端环境：监听touch事件 */
      // if(this.device_width >= 992){
      //   window.addEventListener('scroll', this.handleScroll, true);  // 监听（绑定）滚轮滚动事件
      // }
      // if(this.device_width < 992){
      //   this.listenScroll = true;
      // }
      // this.$on('browserChange',val => {
      //   this.bindListener(val);
      // })
      window.addEventListener('scroll', this.handleScroll, true);
      this.$bus.$on('browserChange',(val) => {
        this.device_width = val;
      })
    },
    computed:{

    },
    methods:{
      // bindListener(val){
      //   window.removeEventListener('scroll', this.handleScroll);
      //   this.listenScroll = false;
      //   if(this.device_width >= 992){
      //     window.addEventListener('scroll', this.handleScroll, true);
      //   }
      //   if(this.device_width < 992){
      //     this.listenScroll = true;
      //   }
      // },
      ContentScroll(position){
        /* 监听移动端touch滚动 */

        /* 该部分代码处理移动端（主要是768px - 1024px）设备的适配，实现的功能为粘性侧边栏
        * 但遇到了问题，fixed属性失效，无法正确定位
        * 可能的原因是：transition属性会导致fixed失效、flex布局会导致fixed失效
        * 目前未能正确找到正确原因，因为在pc端上fixed正常，可能是移动端的某些特性导致
        * 这里使用了一种折中的办法：通过动态赋予top值是侧边栏始终浮动在左侧。 */
        if(position.y < -60 && this.device_width > 992 && this.device_width < 1200){
          let target = -position.y;
          let widget = document.querySelector('#www');
          let widget_width = widget.offsetWidth.toString();
          this.$set(this.fixedStyle,'position','fixed');
          this.$set(this.fixedStyle,'width',widget_width + 'px');
          this.$set(this.fixedStyle,'top',target.toString() + 'px');
        }
        if(position.y > -10 && this.device_width > 992 && this.device_width < 1200){
          this.$set(this.fixedStyle,'position','');
          this.$set(this.fixedStyle,'top','');
        }
      },
      handleScroll(){
        /* 监听pc端滚动事件，如果高度大于60使左侧侧边栏黏性固定
        * （注意！）采用fixed定位后，元素将相对于body定位，如果定位元素是动态设置宽度的，那么此时其宽度可能失调
        *  这里提前记录了一开始的宽度，从而在fixed定位后赋予曾经的宽度值 */
        let scrollHeight = document.documentElement.scrollTop || document.body.scrollTop;
        if(scrollHeight > 60){
          let widget = document.querySelector('#www');
          let widget_width = widget.offsetWidth.toString();
          this.$set(this.fixedStyle,'position','fixed');
          this.$set(this.fixedStyle,'width',widget_width + 'px');
        }
      },
      pullUp(){
        console.log('pullup');
        this.$refs.scroll.finishPullUp();
      },
      pullDown(){
        console.log('pulldown');
        this.$refs.scroll.finishPullDown();
      },
      showWidget(){
        this.showModal = true;
      },
    },
    destroyed() {
      window.removeEventListener('scroll', this.handleScroll);   //  离开页面清除（移除）滚轮滚动事件
    }
  }
</script>

<style scoped lang="less">

  #about{
    width: 100%;
    height: auto;
    background: url(../../assets/img/bg3.gif) repeat;
    position: relative;

    @media screen{
      @media (max-width: 568px){
        height: calc(100vh - 44px);
      }
      @media (min-width: 569px) and (max-width: 992px){
        height: calc(100vh - 56px);
        position: absolute;
        top: 44px;
      }
      @media (min-width: 993px) and (max-width: 1200px){
        height: calc(100vh - 56px);
        position: absolute;
        top: 44px;
      }

    }

    .wrapper{
      height: auto;
      @media screen{
        @media (max-width: 568px){
          position: absolute;
          top: 0;bottom: 0;
          left: 2px;right: 2px;
        }
        @media (min-width: 569px) and (max-width: 1200px){
          position: absolute;
          top: 0;bottom: 0;
          left: 0;right: 0;
        }
      }
    }
  }
</style>